export default "@import '../../themes/src/default.css';\n\n:root {\n    --badge-icon-bg-color: var(--color-light-bg-primary);\n}\n\n.wrapper {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    border-radius: var(--border-radius-circle);\n    box-sizing: border-box;\n\n    &.s {\n        padding: var(--gap-3xs);\n        width: 20px;\n        height: 20px;\n    }\n\n    &.m {\n        padding: var(--gap-3xs);\n        width: 24px;\n        height: 24px;\n    }\n\n    &.xl {\n        padding: 5px;\n        width: 40px;\n        height: 40px;\n    }\n}\n\n.component {\n    @mixin accent_secondary_medium;\n\n    position: relative;\n    z-index: 2;\n    box-sizing: border-box;\n    border-radius: var(--border-radius-pill);\n    overflow: hidden;\n\n    &.background-accent {\n        background-color: var(--color-light-bg-accent);\n        color: var(--color-static-text-primary-light);\n    }\n\n    &.background-specialbg-secondary-transparent {\n        background-color: var(--color-light-specialbg-secondary-transparent);\n        color: var(--color-light-text-primary);\n    }\n\n    &.background-primary {\n        background-color: var(--color-light-bg-primary);\n        color: var(--color-light-text-primary);\n    }\n}\n\n.count {\n    display: inline-flex;\n    justify-content: center;\n    align-items: center;\n    border-radius: var(--border-radius-pill);\n}\n\n.outlineCount {\n    border: 2px solid var(--badge-icon-bg-color);\n}\n\n.s {\n    height: 16px;\n    min-width: 16px;\n    padding: 0 var(--gap-2xs);\n}\n\n.m {\n    height: 20px;\n    min-width: 20px;\n    padding: 0 6px;\n}\n\n.l {\n    height: 24px;\n    min-width: 24px;\n    padding: 0 6px;\n}\n\n.heightS {\n    @mixin accent_secondary_small;\n    padding: 0 var(--gap-2xs);\n}\n\n.heightM {\n    @mixin accent_primary_small;\n    padding: 0 var(--gap-2xs);\n}\n\n.heightL {\n    @mixin accent_primary_small;\n    padding: 0 var(--gap-xs);\n}\n\n.heightXL {\n    @mixin accent_primary_medium;\n    padding: 0 var(--gap-s);\n}\n\n.heightXXL {\n    @mixin accent_primary_medium;\n    padding: 0 var(--gap-m);\n}\n\n.heightS.outlineCount {\n    border: 1px solid var(--badge-icon-bg-color);\n}\n\n.icon {\n    max-height: 30px;\n    min-width: 0;\n    padding: 0;\n    color: var(--color-light-graphic-positive);\n\n    &.positive {\n        color: var(--color-light-graphic-positive);\n        background-color: var(--color-light-graphic-positive);\n    }\n\n    &.attention {\n        color: var(--color-light-graphic-attention);\n        background-color: var(--color-light-graphic-attention);\n    }\n\n    &.negative {\n        color: var(--color-light-graphic-negative);\n        background-color: var(--color-light-graphic-negative);\n    }\n\n    &.link {\n        color: var(--color-light-graphic-link);\n        background-color: var(--color-light-graphic-link);\n    }\n\n    &.tertiary {\n        color: var(--color-light-graphic-tertiary);\n        background-color: var(--color-light-graphic-tertiary);\n    }\n\n    &.secondary {\n        color: var(--color-light-graphic-secondary);\n        background-color: var(--color-light-graphic-secondary);\n    }\n\n    &.primary {\n        color: var(--color-light-graphic-primary);\n        background-color: var(--color-light-graphic-primary);\n    }\n}\n\n.icon.s {\n    width: 16px;\n    height: 16px;\n}\n\n.icon:before {\n    position: absolute;\n    z-index: -1;\n    left: 18%;\n    right: 18%;\n    top: 18%;\n    bottom: 18%;\n    border-radius: var(--border-radius-pill);\n    content: '';\n}\n\n.graphic-static-light.icon:before {\n    background-color: var(--color-static-graphic-light);\n}\n\n.graphic-primary.icon:before {\n    background-color: var(--color-light-graphic-primary);\n}\n\n.outline {\n    background-color: var(--badge-icon-bg-color);\n}\n\n.outlineColor {\n    background-color: var(--color-light-bg-accent);\n\n    &.attention {\n        background-color: var(--color-light-graphic-attention);\n    }\n\n    &.positive {\n        background-color: var(--color-light-graphic-positive);\n    }\n\n    &.negative {\n        background-color: var(--color-light-graphic-negative);\n    }\n\n    &.link {\n        background-color: var(--color-light-graphic-link);\n    }\n\n    &.tertiary {\n        background-color: var(--color-light-graphic-tertiary);\n    }\n\n    &.secondary {\n        background-color: var(--color-light-graphic-secondary);\n    }\n\n    &.primary {\n        background-color: var(--color-light-graphic-primary);\n    }\n}\n\n.dot {\n    width: 8px;\n    height: 8px;\n    min-width: 0;\n    padding: 0;\n}\n\n.dot.outlineCount {\n    border: 1px solid var(--badge-icon-bg-color);\n}\n\n.isHidden {\n    display: none;\n}\n\n.icon svg {\n    position: relative;\n}\n\n.m svg {\n    top: -2px;\n    left: -2px;\n    width: 24px;\n    height: 24px;\n}\n\n.xl svg {\n    width: 30px;\n    height: 30px;\n}\n";